import React from 'react';
import A from '../../helpers/A';
// import Form from '../helpers/Form';

import audio_spot from '../../audios/spot.mp3';
import play from '../../images/pulsarparty/play.png';
import logo from '../../images/pulsarparty/logo.png';
import fb from '../../images/pulsarparty/fb.png';
import rodape_pulsar from '../../images/pulsarparty/rodape/pulsar.png';

import { posts, rodape } from './data';

function Index() {

    const linkEventoFacebook = "http://www.facebook.com/";

    function toggleSpot() {
        if(document.getElementById('spot').paused)
        {
            document.getElementById('spot').play();
        }
        else
        {
            document.getElementById('spot').pause();
            document.getElementById('spot').currentTime = 0;
        }
    }

    let tabIndex = 0;

    function displayTab(index) {
        for(var i = 0; i < numTabs; ++i) {
            document.getElementsByClassName('tab' + i)[0].style.display = "none";
        }
        document.getElementsByClassName('tab' + index)[0].style.display = "";
    }

    function toggleTab() {
        tabIndex = (tabIndex + 1) % numTabs;
        displayTab(tabIndex);
    }

    const numTabs = posts.length;

    return (
        <React.Fragment>
            <link rel="stylesheet" href="../../style/pulsar/pulsar.css" />

            <div id="flyerBody" className="container">
                <div className="row" id="header">
                    <a href={linkEventoFacebook} target="_blank" rel="noopener noreferrer">
                        <img id="logo" className="span5" src={logo} alt="Pulsar Party" />
                    </a>

                    <a href={linkEventoFacebook} target="_blank" rel="noopener noreferrer">
                        <div id="data" className="span6">
                            <p className="dia span2">dia</p>
                            <div className="span3">
                                <p className="data">30/11</p>
                                <p className="hora">de 16:00 às 22:00</p>
                            </div>

                            <img id="facebook-icon" src={fb} />
                        </div>
                    </a>

                    <p id="slogan" className="span6">&nbsp;&nbsp; Eu quero TCHU, eu quero TCHA, <br/> eu vou na festa da PULSAR!</p>

                </div>

                <div id="content">
                    <audio id="spot" autoPlay="autoplay">
                        {/*<source src={audio_spot} type="audio/mp3" preload="auto" />*/}
                    </audio>

                    <div id="slider">
                        <ul className="bjqs">
                        {posts.map( (post, postIndex) =>
                            <li key={postIndex}>
                                <article className={"single tab"+postIndex}>
                                <img className="foto span3" src={post.foto} alt="Foto Principal da postagem"/>
                                <div className={"content span7"+ ((!post.foto || !post.links) & "large")}>
                                    <h2>{post.title}</h2>
                                    <p>{post.text}</p>
                                </div>

                                { !post.links ?
                                    <div>
                                        {/* Não desenha o play */}
                                    </div>
                                : ( (post.links === 'spot') ?
                                    <a className="play span3" onClick={toggleSpot}>
                                        <img src={play} alt="go there" />
                                    </a>
                                : ( ( typeof post.links !== "object" && typeof post.links === "string" ) ?
                                    (<a className="play span3" href={post.links} target="_blank" rel="noopener noreferrer"><img src={play} alt="go there" /></a>)
                                : ( <div className="playContainer span3">
                                        {Object.keys(post.links).map( (dictIndex, index) => (
                                            <React.Fragment>
                                                <a key={index} className="play" href={post.links[dictIndex]} target="_blank" rel="noopener noreferrer">
                                                    <img className="little" src={play} alt="go there" />
                                                    <span>{dictIndex}</span>
                                                </a>
                                                <br />
                                            </React.Fragment>
                                        ))}
                                    </div> )
                                ))}
                                </article>
                            </li>
                        )}
                        </ul>
                    </div>

                    <section className="info">
                        <section className="preco span5">
                            <p>Preços</p>
                            <span>1º lote - R$10,00</span><br />
                            <span>2º lote - R$15,00</span><br />
                            <span>3º lote - R$20,00</span>
                        </section>
                        <section className="local span5">
                            <p>Local</p>
                            <span>UFRJ - Ilha do Fundão</span><br />
                            <span>Campo de futebol ao lado do CCS</span><br />
                            <span>(ao lado da Rodoviária da UFRJ)</span>
                        </section>
                    </section>
                </div>
            </div>

            <div id="silhuetas"> </div>

            <div id="footer">
                <div className="container">
                    <span>Realização</span>
                    <a href="https://www.facebook.com/Pulsar.Consultoria" target="_blank" rel="noopener noreferrer"><img src={rodape_pulsar} alt="Pulsar" /></a>
                    {rodape.map( (elem, index) => (
                        <a key={index} href={elem.link} target="_blank" rel="noopener noreferrer"><img src={elem.image} alt={elem.alt} /></a>
                    ))}
                </div>
            </div>
        </React.Fragment>
    );
}

export default Index;
